<h2 id="modal-对话框" tabindex="-1"><a class="header-anchor" href="#modal-对话框" aria-hidden="true">#</a> Modal 对话框</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="基本用法" tabindex="-1"><a class="header-anchor" href="#基本用法" aria-hidden="true">#</a> 基本用法</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-button @click="WdModal({
        title: '提示',
        content: '确定要提交吗？'
      });">show Modal</wd-button>
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">对话框组件</div>
    <div class="component-desc">
      Modal组件其实是对Dialog组件的封装，默认会有一些操作按钮，并且新增了不同场景下的弹窗样式；弹出一个对话框，提示用户确认信息
    </div>
  </div>
</div>
